    </div>
      <div class="row mt-3">
        <div v-for="(product, idx) in products" :key="product.id" class="col-md-3 mt-md-4">
          <div class="card border-0 mb-4 position-relative">
            <!-- <img
            :src="product.imageUrl[0]"
            class="card-img-top rounded-0"
            alt="..."
            />-->
            <router-link
              :to="`/product-detail/${product.id}`"
              class="product-list-image"
              :style="`background-image: url('${product.imageUrl[0]}');`"
            ></router-link>
            <div class="card-body p-0">
              <h5 class="mb-0 mt-2">{{product.title}}</h5>
              <p class="card-text">{{product.category}}</p>
              <div class="d-flex justify-content-between align-items-center mt-3">
                <p class="card-text mb-0">
                  <!-- {{product.description}} -->
                  ${{product.price}}
                </p>
                <button
                  v-show="loadingBtn(idx)"
                  class="btn btn-outline-dark rounded-0 text-nowrap">
                  <div class="spinner-border text-primary" role="status">
                    <span class="sr-only"></span>
                  </div>
                  Loading...
                </button>
                <button
                  v-show="!loadingBtn(idx)"
                  @click="addCart(product.id, idx)"
                  class="btn btn-outline-dark rounded-0 text-nowrap"
                >加入購物車</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <b-alert
      :show="dismissCountDown"
      dismissible
      fade
      variant="warning"
      @dismiss-count-down="countDownChanged"
      class="mt-3"
      style="position:fixed; bottom:70%; right:15%; width: 70%; z-index: 999;"
    >
      已加入購物車
    </b-alert>
  </div>
</template>

<script>
export default {
  data () {
    return {
      products: {},
      loading: false,
      click: null,
      dismissCountDown: 0
    }
  },
  created () {
    this.getProducts()
  },
  methods: {
    getProducts () {
      this.loading = true
      const apiProducts = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/ec/products`
      this.axios.get(apiProducts).then((res) => {
        this.products = res.data.data
        this.loading = false
      })
    },
    goDetail (id) {
      console.log(this.router)
      // this.router.push(`product-detail/${id}`)
    },
    addCart (id, idx) {
      this.loading = true
      this.click = idx
      const apiAddCart = `${process.env.VUE_APP_APIPATH}/${process.env.VUE_APP_UUID}/ec/shopping`
      this.axios
        .post(apiAddCart, {
          product: id,
          quantity: 1
        })
        .then((res) => {
          console.log(res)
          this.loading = false
        }).catch(err => {
          this.loading = false
          console.log(err)
          this.showAlert()
        })
    },
    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert () {
      this.dismissCountDown = 2
    },
    loadingBtn (idx) {
      return this.loading && this.click === idx
    }
  }
}
</script>

